import styled from 'styled-components';
import MEDIA from 'helpers/mediaTemplates';

// eslint-disable-next-line import/prefer-default-export
export const Section = styled.section`
  display: flex;
  flex-flow: wrap;
  justify-content: space-between;
  margin: 4rem auto;
`;

export const CarouselWrapper = styled.div`
  width: 48%;
  ${MEDIA.PHONE`
    width: 100%;
  `};
`;

export const Description = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 48%;

  .tag {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: 1.2rem;
    color: #707070;

    img {
      margin-right: 1rem;
    }
  }

  h3 {
    margin-bottom: 1.2rem;
  }

  p {
    font-size: 1.7rem;
    color: #707070;

    em {
      display: flex;
      text-align: left;
    }
  }

  .price {
    font-size: 4rem;
  }

  .priceTag {
    color: #707070;
    font-style: italic;
    font-size: 1.7rem;
    margin-bottom: 1.6rem;
  }

  button {
    padding: 0.8rem;
    outline: none;
    border: 1px solid;
    border-radius: 5px;
    box-shadow: 0 13px 16px rgba(255, 76, 0, 0.1);
    color: white;
    font-size: 1.9rem;
    font-weight: bold;
    background-color: #FF4C00;
    transition: all 0.4s ease-in-out;
    cursor: pointer;
    &:hover {
      background: white;
      color: #FF4C00;
      border: 1px solid #FF4C00;
    }
  }

  ${MEDIA.PHONE`
    width: 100%;
  `};
`;
