import React, { Component } from 'react';
import { graphql } from 'gatsby';
import PropTypes from 'prop-types';
import Layout from 'components/layout';
import HeadSection from 'components/headSection';
import SubtitleSection from 'components/subtitleSection';
import ActivitiesSection from 'components/activitiesSection';
import AdventuresSection from 'components/adventuresSection';
import BookingDescription from 'components/bookingDescription';
import ToursSection from 'components/toursSection';
import HtmlFromMarkdown from 'components/htmlFromMarkdown';
import Articles from 'components/articles';
import Mark from 'components/mark';
import Box from 'components/box';
import Wrapper from 'components/wrapper';
import Planet from 'images/ico_planet.png';
import Footer from 'components/footer';

import request from '../lib/api';

export default class Main extends Component {
  state = {
    destinations: {}
  };

  componentDidMount() {
    const params = {
      lang: "en",
      currency: "USD",
    }
    request('mainpage/featured-destinations', 'get', params)
      .then(res => this.setState({ destinations: res.data.data }));
  }

  render() {
    const { data } = this.props;
    const opts = data.homeJson;

    return (
      <Layout>
        <HeadSection data={opts} />
        <Wrapper>
          <ToursSection
            title={opts.toursTitle}
            tours={opts.tours}
            toursCarousels={[opts.toursCarousel1, opts.toursCarousel2]}
          >
            <HtmlFromMarkdown markdown={opts.toursDescription} />
          </ToursSection>
        </Wrapper>
        <Wrapper>
          <AdventuresSection
            title={opts.adventuresTitle}
            adventures={opts.adventures}
          >
            <HtmlFromMarkdown markdown={opts.adventuresDescription} />
          </AdventuresSection>
        </Wrapper>
        <Wrapper>
          <SubtitleSection
            title={opts.readingsTitle}
          >
            <HtmlFromMarkdown markdown={opts.readingsContent} />
          </SubtitleSection>
          <Articles articles={opts.articles} btnBorder={true} />
        </Wrapper>
        <Wrapper>
          <SubtitleSection
            title={opts.communitiesTitle}
          >
            <HtmlFromMarkdown markdown={opts.communitiesContent} />
          </SubtitleSection>
          <Articles articles={opts.communities} btnBorder={false} />
        </Wrapper>
        <Footer />
      </Layout>
    );
  }
}

export const query = graphql`
  query HomepageQuery {
    homeJson {
      headerImageAlt
      title
      days
      country
      cost
      perPerson
      more
      headCarousel {
        childImageSharp {
          fluid(maxHeight: 500, quality: 90) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
      activitiesTitle
      activitiesDescription {
        childMarkdownRemark {
          html
        }
      }
      activities {
        image {
          childImageSharp {
            fluid(maxHeight: 200, quality: 90) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
        title
      }
      toursTitle
      toursDescription {
        childMarkdownRemark {
          html
        }
      }
      tours {
        tag
        title
        description {
          childMarkdownRemark {
            html
          }
        }
        price
        priceTag
        button
      }
      toursCarousel1 {
        childImageSharp {
          fluid(maxHeight: 500, quality: 90) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
      toursCarousel2 {
        childImageSharp {
          fluid(maxHeight: 500, quality: 90) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
      adventuresTitle
      adventuresDescription {
        childMarkdownRemark {
          html
        }
      }
      adventures {
        title
        description
        image {
          childImageSharp {
            fluid(maxHeight: 500, quality: 90) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
      readingsTitle
      readingsContent {
        childMarkdownRemark {
          html
        }
      }
      articles {
        image {
          childImageSharp {
            fluid(maxHeight: 500, quality: 90) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
        href
        title
        content {
          childMarkdownRemark {
            html
          }
        }
      }
      communitiesTitle
      communitiesContent {
        childMarkdownRemark {
          html
        }
      }
      communities {
        image {
          childImageSharp {
            fluid(maxHeight: 500, quality: 90) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
        href
        title
        content {
          childMarkdownRemark {
            html
          }
        }
      }
    }
  }
`;

Main.propTypes = {
  data: PropTypes.shape({
    homeJson: PropTypes.object.isRequired,
  }),
};

Main.defaultProps = {
  data: undefined,
};
