import styled from 'styled-components';
import MEDIA from 'helpers/mediaTemplates';

export const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: white;
  padding: 1rem;
  max-width: 120rem;
  margin-left: auto;
  margin-right: auto;
  margin-top: 46vh;
  ${MEDIA.TABLET`
    margin-top: 30vh;
  `};
  ${MEDIA.PHONE`
    margin-top: 20vh;
  `};
`;

export const Container = styled.div`
  display: flex;
  flex-flow: wrap;
  width: 100%;
`;

export const Selector = styled.input`
  background-color: white;
  padding: 1rem;
  margin: 1rem;
  display: flex;
  align-items: center;
  max-width: 20rem;
  svg {
    margin-right: 1rem;
  }
`;

export const Departure = styled.div`
  display: flex;
  flex-direction: row;
  margin: 1rem;
`;

export const Button = styled.div`
  padding: 1rem 2rem;
  margin: 1rem;
  background-color: #FF4C00;
  border-radius: 3px;
  border: none;
  color: white;
  outline: none;
  cursor: pointer;
  border: 1px solid #FF4C00;
  transition: all 0.4s ease-in-out;
  &:hover {
    background-color: rgba(0, 0, 0, 0);
    color: #FF4C00;
  }
`;
