import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {
  CarouselProvider,
  Slider,
  Slide,
  ButtonBack,
  ButtonNext,
} from 'pure-react-carousel';
import Img from 'gatsby-image';
import Box from 'components/box';
import Title from 'components/title';
import Button from 'components/button';
import Header from 'components/header';
import SearchSection from 'components/searchSection';
import Wrapper from 'components/wrapper';
import ActivitiesSection from 'components/activitiesSection';
import HtmlFromMarkdown from 'components/htmlFromMarkdown';
import bgImg from 'images/banner_bg.png';
import { Carousel, Section, SectionWrapper } from './headSection.css';

class HeadSection extends Component {
  state = { slidesAmount: 2.5 };

  componentDidMount() {
    const slidesAmount = window && window.innerWidth <= 767 ? 1 : 2.5;

    this.setState({
      slidesAmount,
    });
  }

  render() {
    const { data } = this.props;
    const { slidesAmount } = this.state;

    return (
      <Section img={bgImg}>
        <Header altImage={data.headerImageAlt} />
        <SearchSection />
        <SectionWrapper>
          <ActivitiesSection
            title={data.activitiesTitle}
            activities={data.activities}
          >
            <HtmlFromMarkdown markdown={data.activitiesDescription} />
          </ActivitiesSection>
        </SectionWrapper>
      </Section>
    );
  }
}

HeadSection.propTypes = {
  data: PropTypes.shape({
    headCarousel: PropTypes.array.isRequired,
    headerImageAlt: PropTypes.string.isRequired,
    days: PropTypes.string.isRequired,
    country: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    cost: PropTypes.number.isRequired,
    perPerson: PropTypes.string.isRequired,
    more: PropTypes.string.isRequired,
  }).isRequired,
};

export default HeadSection;
