import styled from 'styled-components';
import MEDIA from 'helpers/mediaTemplates';

export const Footer = styled.footer`
  background-color: #3C3C3C;
  color: white;
  display: flex;
  justify-content: center;
`;

export const Wrapper = styled.div`
  display: flex;
  flex-flow: wrap;
  justify-content: space-around;
  width: 100%;
  max-width: 120rem;
  padding: 8rem 4rem 4rem;
  font-size: 10px;

  ${MEDIA.PHONE`
    padding: 6rem 2rem 2rem;
  `};

  .copyRightSection {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    a {
      margin-left: 1rem;
    }
    .copyRight {
      display: flex;
      align-items: center;
    }
    svg {
      margin-right: 0.1rem;
      width: 0.6rem;
    }
  }

  .language {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    p {
      font-size: 1.4rem;
      margin: 0 1rem;
    }
  }
`;

export const SocialSection = styled.div`
  .socialContainer {
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin: 0.4rem 3rem 1rem;
    svg {
      width: 1.4rem;
      height: 1.4rem;
    }
  }
  .policy {
    display: flex;
    flex-flow: wrap;
    font-size: 0.8rem;
    p {
      margin: 0.2rem;
    }
  }
`;
