import React from 'react';
import { Link } from 'gatsby';
import {
  FaMapMarkerAlt,
  FaSwimmer,
  FaRegCalendarCheck
} from "react-icons/fa";
import {
  Wrapper,
  Button,
  Container,
  Selector,
  Departure
} from './searchSection.css';

export default () => (
  <Wrapper>
    <Container>
      <Selector type="text" placeholder="destinations" />
      <Selector type="text" placeholder="activities" />
      <Selector type="text" placeholder="departure date" />
    </Container>
    <Departure>
      Flexible departure
    </Departure>
    <Button>
      <Link to="/search">
        Search
      </Link>
    </Button>
  </Wrapper>
);
