import styled from 'styled-components';
import MEDIA from 'helpers/mediaTemplates';

// eslint-disable-next-line import/prefer-default-export
export const Section = styled.section`
  max-width: 120rem;
  margin: 10rem auto;
  padding: 0 4rem;
  ${MEDIA.TABLET`
    padding: 0 2rem;
  `};
  position: relative;
  display: block;
  text-align: center;
  .title-wrapper, .lead {
    width: 50rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto 1rem;
  }
  .lead {
      font-size: 1.7rem;
      color: #A2A2A2;
      padding-left: 2rem;
   }

  ${MEDIA.DESKTOP`
     &::before {
      font-size: 10vw;
     }
  `};

  ${MEDIA.TABLET`
    margin: 5rem 0;
    &::before {
      display: none;
    }
    .lead {
      font-size: 1.4rem;
    }
  `};

  ${MEDIA.PHONE`
    .title-wrapper, .lead  {
      width: auto;
      display: block;
      padding: 0 10px;
    }
  `};
`;
