import React from 'react';
import PropTypes from 'prop-types';
import Title from 'components/title';
import Img from 'gatsby-image';
import Activity from 'components/activitiesSection/activity';
import { Container, ActivityContainer } from './activitiesSection.css';

const ActivitiesSection = ({ title, subtitle, activities, children }) => (
  <Container>
    <Title as='h3' size='medium'>
      {title}
    </Title>
    <div className='lead'>
      {children}
    </div>
    <ActivityContainer>
      {activities.map(item => (
          <Activity {...item} key={item.title} />
      ))}
    </ActivityContainer>
  </Container>
);

ActivitiesSection.propTypes = {
  activities: PropTypes.arrayOf(PropTypes.object).isRequired,
};

export default ActivitiesSection;
