import React from 'react';
import PropTypes from 'prop-types';
import Title from 'components/title';
import Img from 'gatsby-image';
import Adventure from 'components/adventuresSection/adventure';
import { Container, AdventureContainer } from './adventuresSection.css';

const AdventuresSection = ({ title, adventures, children }) => (
  <Container>
    <Title as='h3' size='medium'>
      {title}
    </Title>
    <div className='lead'>
      {children}
    </div>
    <AdventureContainer>
      {adventures.map(item => (
          <Adventure {...item} key={item.title} />
      ))}
    </AdventureContainer>
  </Container>
);

AdventuresSection.propTypes = {
  title: PropTypes.string.isRequired,
  adventures: PropTypes.arrayOf(PropTypes.object).isRequired,
  children: PropTypes.node.isRequired,
};

export default AdventuresSection;
