import React from 'react';
import PropTypes from 'prop-types';
import Title from 'components/title';
import Wrapper from 'components/wrapper';
import Carousel from 'components/carousel';
import HtmlFromMarkdown from 'components/htmlFromMarkdown';
import groupImg from 'images/group.png';
import { Section, CarouselWrapper, Description } from './tour.css';

const Tour = ({ tour, toursCarousel }) => {
  return (
    <Section>
      <CarouselWrapper>
        <Carousel images={toursCarousel} />
      </CarouselWrapper>
      <Description>
        <div className="tag">
          <img src={groupImg} alt="group" />
          {tour.tag}
        </div>
        <Title as='h3' size='medium'>
          {tour.title}
        </Title>
        <HtmlFromMarkdown markdown={tour.description} />
        <div className="price">
          {tour.price}
        </div>
        <div className="priceTag">
          {tour.priceTag}
        </div>
        <button>{tour.button}</button>
      </Description>
    </Section>
  )
}

Tour.propTypes = {
  tour: PropTypes.object.isRequired,
  toursCarousel: PropTypes.arrayOf(PropTypes.object).isRequired,
};

export default Tour;
