import React from 'react';
import PropTypes from 'prop-types';
import Title from 'components/title';
import { Section } from './toursSection.css';
import Tour from './tour/tour';

const ToursSection = ({ title, tours, toursCarousels, children }) => (
  <Section>
    <Title as='h3' size='medium'>
      {title}
    </Title>
    <div className='lead'>
      {children}
    </div>
    {tours.map((tour, idx) => (
      <Tour
        key={idx}
        tour={tour}
        toursCarousel={toursCarousels[idx]}
      />
    ))}
  </Section>
)

ToursSection.propTypes = {
  title: PropTypes.string.isRequired,
  tours: PropTypes.arrayOf(PropTypes.object).isRequired,
  toursCarousels: PropTypes.array.isRequired,
  children: PropTypes.node.isRequired,
};

export default ToursSection;
